  font-weight: 500;
  font-style: normal;
  font-display: swap; }

html {
  box-sizing: border-box;
  overflow-x: hidden;
  height: 100%; }

.disable-scroll {
  overflow: hidden; }

*,
*::before,
*::after {
  box-sizing: inherit; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden; }

body {
  padding: 0;
  margin: 0 auto;
  font-family: "avenir", "Arial", sans-serif;
  font-size: 14px;
  line-height: 20px;
  font-weight: 300;
  color: #000000;
  background-color: #ffffff;
  height: 100%; }

img {
  max-width: 100%; }

a {
  text-decoration: none; }
  a:hover, a:active {
    opacity: 0.5; }

.page-wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100%; }

.page-main,
.page-catalog,
.page-card {
  flex: 1 0 auto; }

/*
.page-header {
  flex: 1 0 auto;
}*/
.page-header {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between; }

@media (min-width: 1280px) {
  .page-header__search {
    width: 728px; } }

@media (min-width: 1280px) {
  .page-header__search input {
    width: 100%;
    height: 37px;
    border: 1px solid #dfdada;
    padding: 0; } }

@media (min-width: 1280px) {
  .page-header__search label {
    position: relative; }
    .page-header__search label::after {
      position: absolute;
      content: url("../img/search.png");
      width: 21px;
      height: 21px;
      top: 0;
      right: 10px;
      display: block;
      z-index: 100; } }

.container {
  min-width: 320px;
  padding: 26px 23px;
  margin: 0 auto; }
  @media (min-width: 640px) {
    .container {
      width: 640px;
      padding: 23px 31px; } }
  @media (min-width: 1280px) {
    .container {
      width: 1280px;
      padding: 28px 62px; } }

@media (min-width: 1280px) {
  .intro {
    background-color: green;
    background-image: url("../img/intro-desktop@1x.png");
    background-repeat: no-repeat;
    background-position: center center;
    background-size: 100%;
    padding-top: 110px;
    padding-bottom: 166px; } }
  @media (min-width: 1280px) and (min-resolution: 144dpi), (min-width: 1280px) and (min-resolution: 1.5dppx) {
